@charset "UTF-8";
/*ГЛОБАЛЬНЫЕ СТИЛИ*/
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../font/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../font/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../font/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

html {
  height: 100%; }

body {
  display: grid;
  grid-template-rows: min-content 1fr;
  min-width: 320px;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/*БЛОКИ*/
.page-body {
  max-width: 1920px;
  background-color: #131313;
  color: #ffffff; }

.button {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  padding: 21px;
  box-sizing: border-box;
  transform: skewX(-17deg);
  font-weight: 700;
  font-size: 12px;
  line-height: 150%;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #C2AB81;
  border: none;
  box-shadow: 6px 0 #131313, 12px 0 #ffffff; }

.button span {
  display: block;
  transform: skewX(17deg); }

.page-header {
  width: 100%;
  padding: 28px 5.2%;
  margin: 0 auto;
  box-sizing: border-box; }

.page-header__navigation {
  display: flex;
  align-items: center;
  width: 100%; }

.page-header__button {
  display: none; }

.page-header__logo-link {
  width: 156px;
  height: 60px;
  margin-right: 4.4%; }

.page-header__logo-link img {
  width: 156px;
  height: 60px; }

.page-header__wrapper-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 86.5%; }

.page-header__list-navigation-site {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 42px;
  row-gap: 10px;
  align-items: center;
  width: 55.6%; }

.page-header__link-navigation-site {
  font-weight: 700;
  font-size: 14px;
  line-height: 150%;
  text-transform: uppercase;
  color: #ffffff; }

.page-header__list-navigation-user {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 26.7%; }

.page-header__item-navigation-user--phone {
  width: 19%;
  margin-right: 6.3%;
  margin-left: 3%; }

.page-header__item-navigation-user--phone span {
  display: none; }

.page-header__link-navigation-user--phone {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  height: 60px;
  box-sizing: border-box;
  transform: skewX(-17deg);
  background-color: transparent;
  border: 1px solid #ffffff; }

.page-header__link-navigation-user--phone svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) skewX(17deg);
  width: 20px;
  height: 20px; }

.page-header__item-navigation-user--request {
  width: 67%; }
